import { useEffect, useState } from "react";
import { ReactSortable } from "react-sortablejs";

const PlayerCollection = ({
	mana,
	setMana,
	disabled,
	deck,
	useDeck,
	className,
	cardClass,
	pull,
	put,
	setDrawn,
}) => {
	const [toPlay, setToPlay] = useState(null);

	const [playerDeck, setPlayerDeck] = useState(deck);

	useEffect(() => {
		// if (className === "playerField") {
		// 	console.log("CHANGES");
		// 	console.log(deck);
		// }
		setPlayerDeck([...deck]);
	}, [deck]);

	// useEffect(() => {
	// 	console.log(toPlay);
	// }, [toPlay]);

	function isAffordable(card) {
		return card.mana <= mana ? "affordableCard" : "handCard";
	}

	function getClass(className, card) {
		if (className !== "playerField") return "backStack";
		if (className === "playerField") {
			if (card.attacking) return "tapped attackPlayer";
			return card.tapped ? "tapped" : "card";
		}
		return "card";
	}

	if (className === "playerHand")
		return (
			<ReactSortable
				sort={false}
				className={"playerHand"}
				disabled={disabled}
				list={playerDeck}
				setList={useDeck}
				group={{
					name: className,
					put: put,
					pull: pull,
				}}
				fallbackOnBody={true}
				invertSwap={true}
				forceFallback={false}
				// draggable={[".affordable", ".card"]}
				// dragoverBubble={true}
				onStart={function (evt) {
					// console.log("meow");
					setToPlay(playerDeck[evt.oldIndex]);
				}}
				onMove={function (evt) {
					if (evt.to.className === "playerField") {
						// console.log(toPlay);
						if (toPlay.mana <= mana) {
							// setMana(mana - toPlay.mana);
							return true;
						} else return false;
					} else return true;
				}}
				onRemove={function (evt) {
					if (evt.to.className === "playerField") {
						// console.log(toPlay);
						if (toPlay.mana <= mana) setMana(mana - toPlay.mana);
					}
				}}
			>
				{playerDeck
					// .filter((card) => card.mana <= mana)
					.map((card) => (
						<div className="playerCard" key={card.id}>
							<div className={isAffordable(card)}>
								<img
									className={cardClass}
									src={card.img}
									alt={cardClass}
									draggable="false"
								/>
								<span className="badge">
									{card.damage} / {card.health}
								</span>
							</div>
						</div>
					))}
			</ReactSortable>
		);
	else
		return (
			<ReactSortable
				sort={pull}
				disabled={disabled}
				className={className}
				list={playerDeck}
				setList={useDeck}
				group={{
					name: className,
					// put: function (evt) {
					// 	if (className === "playerField") console.log(evt);
					// },
					put: function (to) {
						// console.log(to);
						if (className === "playerField") {
							// 	console.log(toPlay);
							if (to.el.children.length < 5) return true;
							else return false;
						} else return put;
					},
					pull: pull,
				}}
				onRemove={function (evt) {
					if (
						evt.from.className === "playerDeck" &&
						(evt.to.className === "playerHand" ||
							evt.to.className === "playerDiscard")
					) {
						setDrawn(true);
					}
				}}
			>
				{playerDeck.map((card) => (
					<div className={getClass(className, card)} key={card.id}>
						<img
							className={cardClass}
							src={cardClass === "front" ? card.img : card.cover}
							alt={cardClass}
							draggable="false"
						/>
						{className === "playerField" ? (
							<span className="badge">
								{card.damage} / {card.health}
							</span>
						) : (
							""
						)}
					</div>
				))}
			</ReactSortable>
		);
};

export default PlayerCollection;
